<template>
    <div class="identify">
      <div class="backgroundArea" :style="{ backgroundImage: 'url('+ backgroundImg +')'}">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle"></span>
            <span class="back"></span>
        </div>
        <div class="content">
            <div class="title">身份选择</div>
            <button userType="button" @click="chooseId(index)" v-for="(id, index) in ids" :key="index" :class="{active: id.isActive}">{{ id.become }}</button>
            <div class="notice">温馨提示:选择身份后将无法修改,请慎重选择!</div>
        </div>
        <div class="ensure" @click="ensure">
          <img src="../../assets/img/verify.png">
        </div>
      </div>
    </div>
</template>

<script>
import { regist } from '../../api/getdata'
// import { setCookie, getCookie } from '../../../static/js/cookieUtil'
import { Toast } from 'mint-ui'
export default {
  name: 'identify',
  data () {
    return {
      linkTo: '/home/index',
      registUserType: 'M',
      backgroundImg: require('../../assets/img/login.png'),
      enrollForm: {
        account: '',
        verificationCode: '',
        password: ''
      },
      ids: [{
        become: '我是学员', userType: 'M', linkTo: '/home/index', isActive: true
      }, {
        become: '我是教练', userType: 'T', linkTo: '/home/enroll/validate', isActive: false
      }, {
        become: '我是拳馆', userType: 'C', linkTo: '/home/enroll/validate', isActive: false
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    chooseId (index) {
      this.linkTo = this.ids[index].linkTo
      this.registUserType = this.ids[index].userType
      for (var i = 0; i < this.ids.length; i++) {
        this.ids[i].isActive = false
        this.ids[index].isActive = true
      }
      // console.log(this.linkTo)
      // console.log(this.registUserType)
    },
    ensure () {
      console.log(this.linkTo)
      let data = regist({
        fPassword: localStorage.getItem('registePassword'), fUserType: this.registUserType, fMobile: localStorage.getItem('registeAccount'), code: localStorage.getItem('registeVerificationCode'), invitationCode: localStorage.getItem('inviteCode')
      })
      data.then(res => {
        console.log(res)
        if (res.status === '-1') {
          Toast({
            message: '注册失败',
            duration: 1000
          })
          this.$router.go(-1)
        } else if (res.status === '0') {
          Toast({
            message: '注册成功',
            duration: 1000
          })
          localStorage.setItem('account', this.enrollForm.account)
          localStorage.setItem('password', this.enrollForm.password)
          localStorage.setItem('userType', this.registUserType)
          localStorage.setItem('userState', 'isLogin')
          localStorage.setItem('userId', res.data.fUserId)
          this.$router.push(this.linkTo)
        }
      })
    }
  }
}
</script>
<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .backgroundArea {
    width: 100%;
    height: 17.786667rem;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  .content {
    width: calc(100% - 1.26667rem);
    margin: 0 auto;
  }

  .content button {
    display: block;
    width: 100%;
    background: #ffffff;
    border: 1px solid #ffd200;
    outline: none;
    margin: 0.93333rem auto;
    font-size: 0.426667rem;
    color: #ffd200;
    height: 1.3333rem;
    border-radius: 0.15rem;
    font-weight: 400;
  }

  .content button.active {
    background: #ffd200;
    color: #ffffff;
  }

  .title {
    font-size: 0.53333rem;
    color: #333333;
    margin-top: 4.2rem;
  }

  .notice {
    font-size: 0.37333rem;
    color: #333333;
    margin-top: -0.32rem;
  }

  .ensure {
    width: 1.36rem;
    height: 1.36rem;
    margin: 0 auto;
    margin-top: 1rem;
  }
</style>



